<template>
  <div>
    <router-link to="/" class="mainpage">На главную</router-link>
    <div class="container">
      <div class="card-info">
        <div class="card-title">{{cardName}}</div>
      </div>
      <div class="card-img">
        <img :src=(cardImg) alt="" v-if="cardImg">
        <div class="card-desc" v-else v-html="cardDesc"></div>
      </div>
    </div>
  </div>
</template>

<script>

  import {mapGetters} from 'vuex';

  export default {
    name: "Card",
    computed: {
      ...mapGetters(['getSingleCard']),
      cardName() {
        return this.getSingleCard[0]['card_name'].english
      },
      cardImg() {
        return this.getSingleCard[0]['large_image'].default
      },
      cardDesc() {
        return this.getSingleCard[0]['card_text'].english
      }
    },
    created() {
      let id = +this.$route.path.slice(1);
      this.$store.dispatch('changePageID', id);
    }
  }
</script>

<style scoped lang="scss">
  .mainpage {
    padding: 20px;
    text-align: center;
    background-color: #1abc9c;
    display: block;
    text-decoration: none;
    color: #ffffff;
    font-weight: bold;
    font-size: 30px;
  }

  .container {
    max-width: 1200px;
    width: 100%;
    margin: 30px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 15px;
  }

  .card-img {
    width: 300px;
    min-width: 300px;
    img {
      width: 100%;
    }
  }

  .card-info {
    width: 100%;
  }

  .card-title {
    text-align: center;
    font-weight: bold;
    font-size: 40px;
    margin-bottom: 30px;
  }
</style>
