<template>
  <div class="card-wrapper">
    <router-link :to="'/' + cardID" class="card" :class="color" @mouseenter.native="showHover" @mouseleave.native="hideHover">
      <img class="card-icon" :src=(cardIcon) v-if="cardIcon">
      <div class="card-title">{{title}}</div>
      <div class="card-hover" v-if="active">
        <img :src=(img) alt="" v-if="img">
        <div class="card-description" v-else v-html="cardText"></div>
      </div>
    </router-link>
  </div>
</template>

<script>
  export default {
    name: "Card",
    props: [
      'title',
      'img',
      'cardType',
      'cardIcon',
      'cardText',
      'cardID',
      'isBlue',
      'isBlack',
      'isGreen',
      'isRed',
    ],
    data() {
      return {
        active: false
      }
    },
    methods: {
      showHover() {
        this.active = true
      },
      hideHover() {
        this.active = false
      }
    },
    computed: {
      colorClass() {
        return this.cardType.toLowerCase().replace(/\s/g, '');
      },
      color() {
        if (this.isBlue) {
          return 'blue'
        } else if (this.isBlack) {
          return 'black'
        } else if (this.isGreen) {
          return 'green'
        } else if (this.isRed) {
          return 'red'
        } else {
          return 'gray'
        }
      }
    },
  }
</script>

<style scoped lang="scss">
  .card-wrapper {
    width: 20%;
    padding: 0 5px;
  }

  @media (max-width: 991px) {
    .card-wrapper {
      width: 33.333%;
    }
  }

  @media (max-width: 575px) {
    .card-wrapper {
      width: 50%;
    }
  }

  @media (max-width: 479px) {
    .card-wrapper {
      width: 100%;
    }
  }

  .card {
    width: 100%;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    margin-bottom: 10px;
    position: relative;
    color: rgba(#fff, .7);
    display: flex;
    align-items: center;
    text-decoration: none;
    &.hero {
      background-color: #7f8c8d;
    }
    &.spell {
      background-color: #c0392b;
    }
    &.passiveability {
      background-color: #1abc9c;
    }
    &.creep {
      background-color: #95a5a6;
    }
    &.ability {
      background-color: #e67e22;
    }
    &.improvement {
      background-color: #8e44ad;
    }
    &.item {
      background-color: #3498db;
    }
    &.blue {
      background-color: #0f5978;
    }
    &.black {
      background-color: #312B2D;
    }
    &.green {
      background-color: #5B7A46;
    }
    &.red {
      background-color: #9B2D3A;
    }
    &.gray {
      background-color: #95a5a6;
    }
  }

  .card-title {
    font-weight: bold;
  }

  .card-icon {
    width: 20px;
    margin-right: 8px;
  }

  .card-hover {
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    z-index: 10;
    img {
      width: 100%;
    }
  }

  .card-description {
    background-color: #000000;
    padding: 15px;
  }
</style>
